import Swal from 'sweetalert2';
import axios from "axios";
import { AgGridVue } from "ag-grid-vue3";
export default {
  components: {
    AgGridVue,
    Swal
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      rowData: [],
      searchType: "c.check_code",
      // 검색 조건 
      searchText: "",
      // 검색어
      columnDefs: [{
        field: "check_code",
        headerName: "검사코드",
        flex: 1
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 2
      }, {
        field: "mat_order_detailCode",
        headerName: "주문디테일",
        flex: 1
      }, {
        field: "check_quantity",
        headerName: "검수량",
        flex: 1
      }, {
        field: "check_history",
        headerName: "검수내역",
        flex: 2
      }, {
        field: "check_status",
        headerName: "합격여부",
        flex: 1,
        cellStyle: params => {
          if (params.value == '합격') {
            return {
              color: '#22C55E',
              textAlign: 'center',
              fontWeight: 'bold'
            };
          } else if (params.value == '불합격') {
            return {
              color: '#E02D2D',
              textAlign: 'center',
              fontWeight: 'bold'
            };
          }
        }
      }, {
        field: "mat_error_name",
        headerName: "불합격사유",
        flex: 3,
        cellStyle: params => {
          return {
            color: '#E02D2D',
            textAlign: 'center',
            fontWeight: 'bold'
          };
        }
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.checkResultList();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    checkResultList() {
      axios.get('/api/mat/checkResultList').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    searcCheckResultList() {
      axios.get('/api/mat/checkResultList', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    async clicked(event) {
      let result = await axios.get('/api/mat/finExist/' + event.data.check_code);
      console.log(result);
      if (result.data[0].checkCount > 0) {
        Swal.fire({
          title: '실패',
          text: '이미 완제품 입고가 진행된 자재입니다..',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      //만약 완제품입고가 완료된 항목에 대해선 validation 추가해야함
      this.$emit('checkResult', event.data);
      this.close();
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.checkResultList(); // visible이 true로 바뀌면 쿼리 실행
      }
    }
  }
};